import * as React from 'react';
import { motion, useViewportScroll } from "framer-motion";
import DragHandleIcon from '@mui/icons-material/DragHandle';
import IconButton from '@mui/material/IconButton';
import { NavLink } from "react-router-dom";




export default function Navbar(props) {
  const navStyles = {

    display: "flex",

    alignItems: "center",
    justifyContent: "space-between",
    height: "6rem",

    width: "80vw",
    left: "0",
    top: "0",
    fontFamily: 'lightFont',

}

const variantLinks = {
  hidden: {
      opacity: 0, display: 'none'
  },
  onscreen: {
      display: 'none',
      opacity: 1
  }
}
  const { scrollY } = useViewportScroll();
  /** this hook manages state **/
  const [hidden, setHidden] = React.useState(false);
  const [showLinks, setShowLinks] = React.useState(false);


  /** this onUpdate function will be called in the `scrollY.onChange` callback **/
  function update() {
      if (scrollY?.current < scrollY?.prev) {
          setHidden(false);
      } else if (scrollY?.current > 10 && scrollY?.current > scrollY?.prev) {
          setHidden(true);
      }
  }

  /** update the onChange callback to call for `update()` **/
  React.useEffect(() => {
      return scrollY.onChange(() => update());
  });

  const changeNav = () => {
    setShowLinks(prevCheck => !prevCheck);
    console.log("clicked")
}

  const variants = {

    visible: { opacity: 1, y: 0, color: '#FFFFFF', backgoundColor: '#000000' },

    hidden: { opacity: 0, z: +200 }
};


  return (
    <div>
         <motion.nav
                variants={variants}
                className="navBarMotion"
                animate={hidden ? "hidden" : "visible"}

                transition={{ ease: [0.1, 0.25, 0.3, 1], duration: 0.3 }}

                style={navStyles}
            >
                <motion.nav
                    variants={variantLinks}
                    className="navBarMotion"
                    animate={showLinks ? "visible" : "hidden"}

                    transition={{ ease: [0.1, 0.25, 0.3, 1], duration: 0.3 }}

                    style={navStyles}

                > <div className="linkContainer">
                    <NavLink className="tabLinkItems" to={props.nav1}>
                    <span>{props.link1}</span>
              
            </NavLink>
            <NavLink className="tabLinkItems" to={props.nav2}>
                    <span>{props.link2}</span>
              
            </NavLink>
            
                   
                
                </div></motion.nav>


                <motion.h1 variants={variantLinks} animate={showLinks ? "hidden" : "visible"}>LJSDesign</motion.h1>

                <IconButton onClick={changeNav} color="inherit" fontSize="40px" sx={{ fontSize: "40px" }} ><DragHandleIcon fontSize="40px" /></IconButton>

            </motion.nav>
      
     
    </div>
  );
}
