import React from 'react'
import Navbar from '../components/Navbar';
import GitHubIcon from '@mui/icons-material/GitHub';
import IconButton from '@mui/material/IconButton';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import { motion, AnimatePresence } from 'framer-motion';
export default function Description() {
  return (
      <div className="descBackground">
      <motion.div initial={{opacity: 0}} animate={{ opacity: 1}}  className='navWrapper'>
          <Navbar link1={"Home"} nav1={"/"} link2={"Contact"} nav2={"/contact"}/>
    <div className='descContainer'>
        <div className='descTitleContainer'>
            <h1 className='descTitle'>About</h1>

        </div>
        <div className='descSubTitleContainer'>
            <p className='descSubTitle'>
                I'm a developer/designer with three years of professional experience creating full-stack web applications for clients. You can see more of my work here: 
            </p>
        </div>
        <div className='descLinksContainer'>
        <IconButton sx={{fontSize: '50px'}} onClick={ () =>  window.open('https://github.com/kuel321?tab=repositories')}>< GitHubIcon sx={{color: 'white', fontSize: '50px'}} ></GitHubIcon></IconButton>
        <IconButton sx={{fontSize: '50px'}} onClick={ () =>  window.open('https://www.linkedin.com/in/luke-short-272434b8/')}>< LinkedInIcon sx={{color: 'white', fontSize: '50px'}} ></LinkedInIcon></IconButton>
        </div>
    </div>
    </motion.div>
    </div>
  )
}
