

import React from "react";
import { motion, useViewportScroll } from "framer-motion";
import DragHandleIcon from '@mui/icons-material/DragHandle';
import IconButton from '@mui/material/IconButton';
import { NavLink } from "react-router-dom";
import project1 from '../project1.jpg';

import Body from '../components/Body';


const navStyles = {

    display: "flex",

    alignItems: "center",
    justifyContent: "space-between",
    height: "6rem",

    width: "80vw",
    left: "0",
    top: "0",
    fontFamily: 'lightFont',

};



export default function Nav() {
    /** this hook gets the scroll y-axis **/
    const { scrollY } = useViewportScroll();
    /** this hook manages state **/
    const [hidden, setHidden] = React.useState(false);
    const [showLinks, setShowLinks] = React.useState(false);
    const [color, setColor] = React.useState('blue');

    /** this onUpdate function will be called in the `scrollY.onChange` callback **/
    function update() {
        if (scrollY?.current < scrollY?.prev) {
            setHidden(false);
        } else if (scrollY?.current > 10 && scrollY?.current > scrollY?.prev) {
            setHidden(true);
        }
    }

    /** update the onChange callback to call for `update()` **/
    React.useEffect(() => {
        return scrollY.onChange(() => update());
    });
    const changeNav = () => {
        setShowLinks(prevCheck => !prevCheck);
        console.log("clicked")
    }
    const changeColor = () => {
        console.log()
        setColor('#FFFFFF')
    }
    const variants = {

        visible: { opacity: 1, y: 0, color: '#FFFFFF', backgoundColor: '#000000' },

        hidden: { opacity: 0, z: +200 }
    };
    const titleVariant = {
        offscreen: {
            backgoundColor: '##FFFFFF',
            initial: {
                x: -20,
            }


        },
        onscreen: {
            backgroundColor: '#000000',

            color: '#FFFFFF',
            transition: {
                marginTop: 0,
                duration: 2,
                type: 'spring',
                ease: 'easeIn'
            }


        }
    }
    const variantLinks = {
        hidden: {
            opacity: 0, display: 'none'
        },
        onscreen: {
            display: 'none',
            opacity: 1
        }
    }
    const subTitleVariant = {
        offscreen: {

            backgoundColor: '##FFFFFF',
            initial: {
                x: -20,
            }


        },
        onscreen: {
            backgroundColor: '#000000',

            color: '#FFFFFF',
            transition: {
                marginTop: 0,
                duration: 2,
                type: 'spring',
                ease: 'easeIn'
            }


        }
    }

    const bodyVariant = {
        offscreen: {
            display: 'none'
        },
        onscreen: {
            opacity: 1
        }
    }
    const subTitleVariant2 = {
        offscreen: {
            color: '#000000',
            backgoundColor: '#000000',
            initial: {
                x: -20,
            }


        },
        onscreen: {
            backgroundColor: '#FFFFFF',

            color: '#000000',
            transition: {
                marginTop: 0,
                duration: 2,
                type: 'spring',
                ease: 'easeIn'
            }


        }
    }
    return (
        <div className="navWrapper">
            <motion.nav
                variants={variants}
                className="navBarMotion"
                animate={hidden ? "hidden" : "visible"}

                transition={{ ease: [0.1, 0.25, 0.3, 1], duration: 0.3 }}

                style={navStyles}
            >
                <motion.nav
                    variants={variantLinks}
                    className="navBarMotion"
                    animate={showLinks ? "visible" : "hidden"}

                    transition={{ ease: [0.1, 0.25, 0.3, 1], duration: 0.3 }}

                    style={navStyles}

                > <div className="linkContainer">
                        <NavLink className="tabLinkItems" to='/about'>
                            <span>About</span>

                        </NavLink>
                        <NavLink className="tabLinkItems" to='/contact'>
                            <span>Contact</span>

                        </NavLink>

                        </div></motion.nav>


                <motion.h1 variants={variantLinks} animate={showLinks ? "hidden" : "visible"}>LJSDesign</motion.h1>

                <IconButton onClick={changeNav} color="inherit" fontSize="40px" sx={{ fontSize: "40px" }} ><DragHandleIcon fontSize="40px" /></IconButton>

            </motion.nav>
            <motion.div variants={titleVariant} initial="offscreen"
                whileInView="onscreen"
                viewport={{ amount: 1 }} className="headerTitle">
                <h1>
                    Hi, I'm Luke.
                </h1>


            </motion.div>
            <motion.div variants={subTitleVariant} initial="offscreen"
                whileInView="onscreen"
                viewport={{ amount: 1 }} className="headerSubTitle">
                <h1>I make websites</h1>
            </motion.div>

            <motion.div variants={subTitleVariant2} initial="offscreen"
                whileInView="onscreen"
                viewport={{ amount: 0.2 }} className="portContainer">
                <h1 className="portfolioTitle">Like these</h1>
                <motion.div variants={{ offscreen: { opacity: 0 }, onscreen: { opacity: 1 }  }} initial="offscreen" transition={{delay: 0.5}}
                    whileInView="onscreen"
                    viewport={{ amount: 0.2 }} className="portBody">
                    <Body />
                </motion.div>

            </motion.div>

        </div>

    );
}
