import React from 'react'
import { motion, useViewportScroll, Variants } from "framer-motion";
export default function Marquee() {
    const marqueeEffect = {
        animate: {
          x: ['120vw', '-120vw'],
          transition: {
            x: {
              repeat: Infinity,
              repeatType: "loop",
              duration: 20,
              ease: "linear",
            },
          },
        },
      };
  return (

    <div>
        <motion.div className="aboutBody">
            <motion.h1 variants={marqueeEffect} className="marqueeTitle" animate="animate">
                Digital Branding / Full-Stack Applications / Animations
            </motion.h1>
        </motion.div>
    </div>
  )
}
