import {React, useState }from 'react'

import proj1 from '../project1.jpg';
import proj2 from '../project2.jpg';
import proj3 from '../project3.jpg';
import { motion, useViewportScroll, Variants } from "framer-motion";
import Marquee from './Marquee';
export default function Body() {

    const [isHovered, setHovered] = useState(false)
    const variants = {

        offscreen: {
            backgoundColor: '##FFFFFF',

        },
        onscreen: {
            backgroundColor: '#000000',

            color: '#FFFFFF',
            transition: {
                marginTop: 0,
                duration: 2,
                type: 'spring',
                ease: 'easeIn'
            }


        }
    };


    return (
        <motion.div className='portfolio'>
            <motion.div  className='portImageBody'>
                <div className='portTitle'>
                    <h1>Wv Traffic</h1>
                </div>
                <div className='portImage'>
                <motion.img  onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)} whileHover={{
    scale: 1.1,
    transition: { duration: 0.5 },
  }}
  className='pImg' src={proj1} />
                </div>
               
            </motion.div>
            <motion.div className='portImageBody'>
                <div className='portTitle'>
                    <h1>Wv Weather</h1>
                </div>
                <div className='portImage'>
                <motion.img whileHover={{
    scale: 1.1, filter: 'grayscale(100%)', 
    transition: { duration: 0.5 },
  }}
  className='pImg' src={proj2} />
                </div>
               
            </motion.div>
            <motion.div className='portImageBody'>
                <div className='portTitle'>
                    <h1>Teays Valley Hardware</h1>
                </div>
                <div className='portImage'>
                <motion.img whileHover={{
    scale: 1.1,
    transition: { duration: 0.5 },
  }}
   className='pImg' src={proj3} />
                </div>
               
            </motion.div>
        </motion.div>
    )
}
